import { API } from './API'
import { lang } from './Lang'
import { log } from './Log'
import { toast } from './Toast'
import { token } from './Token'
import { states } from './store/States'
import { WorkBookmarkData } from './Bookmark'
import { setTimeoutWorker } from './SetTimeoutWorker'
import { Config } from './Config'
import { settings } from './setting/Settings'

class UnBookmarkWorks {
  public async start(list: WorkBookmarkData[]) {
    log.warning(lang.transl('_'))
    if (list.length === 0) {
      toast.error(lang.transl('_'))
      log.error(lang.transl('_'))
      return
    }

    states.busy = true

    const total = list.length
    log.log(lang.transl('_', total.toString()))

    //  deleteBookmark  API  429 
    // 48 
    const slowMode = total > 48

    let progress = 0

    for (const item of list) {
      try {
        await this.waitSlowMode(slowMode)
        await API.deleteBookmark(item.bookmarkID, item.type, token.token)
      } catch (error) {
        //  404 
        // 
        // 
      }
      progress++
      log.log(`${progress} / ${total}`, 1, false)
    }

    const msg = lang.transl('_') + ' ' + lang.transl('_')
    log.success(msg)
    toast.success(msg, {
      position: 'topCenter',
    })
    states.busy = false
  }

  private waitSlowMode(slowMode: boolean): Promise<void> {
    return new Promise((resolve) => {
      if (!slowMode) {
        return resolve()
      } else {
        setTimeoutWorker.set(() => {
          return resolve()
        }, settings.slowCrawlDealy)
      }
    })
  }
}

const unBookmarkWorks = new UnBookmarkWorks()
export { unBookmarkWorks }
