import { lang } from './Lang'
import { Config } from './Config'
import { msgBox } from './MsgBox'
import { Utils } from './utils/Utils'
import { EVT } from './EVT'
import { setSetting, settings } from './setting/Settings'

// 
class ShowWhatIsNew {
  constructor() {
    this.bindEvents()
  }

  private flag = '17.1.3'

  private bindEvents() {
    window.addEventListener(EVT.list.settingInitialized, () => {
      //  settingInitialized 
      let msg = `
      <span>${lang.transl('_v1713')}</span>
      `

      // <strong><span>${lang.transl('_')}:</span></strong>
      // <span class="blue">${lang.transl('_')}</span>

      // ${lang.transl(
      //   '_xx',
      //   lang.transl('_')
      // )}

      // <br>
      // <span>${lang.transl('_')}</span>
      // <br>
      // <span>${lang.transl('_')}</span>

      // <span>${lang.transl('_')}</span>

      // 
      msg += `
      <br>
      <br>
      ${lang.transl('_')}`

      this.show(msg)
    })
  }

  private show(msg: string) {
    if (Utils.isPixiv() && settings.whatIsNewFlag !== this.flag) {
      msgBox.show(msg, {
        title: Config.appName + ` ${lang.transl('_')}`,
        btn: lang.transl('_'),
      })
      setSetting('whatIsNewFlag', this.flag)
    }
  }
}

new ShowWhatIsNew()
