import { lang } from './Lang'
import { log } from './Log'
import { toast } from './Toast'
import { states } from './store/States'
import { bookmark, WorkBookmarkData } from './Bookmark'

class RemoveWorksTagsInBookmarks {
  public async start(list: WorkBookmarkData[]) {
    if (list.length === 0) {
      toast.error(lang.transl('_'))
      log.error(lang.transl('_'))
      return
    }

    states.busy = true

    const total = list.length.toString()
    log.log(lang.transl('_', total))

    let number = 0
    for (const item of list) {
      try {
        await bookmark.add(
          item.workID.toString(),
          item.type,
          [],
          false,
          item.private,
          true
        )
      } catch (error) {
        //  404 
        //  429  429 
      }
      number++
      log.log(`${number} / ${total}`, 1, false)
    }

    const msg =
      lang.transl('_') + ' ' + lang.transl('_')
    log.success(msg)
    toast.success(msg, {
      position: 'topCenter',
    })
    states.busy = false
  }
}

const removeWorksTagsInBookmarks = new RemoveWorksTagsInBookmarks()
export { removeWorksTagsInBookmarks }
