import { EVT } from '../EVT'
import { lang } from '../Lang'

// 
class ShowSkipCount {
  constructor(el: HTMLElement) {
    this.el = el
    lang.register(this.el)
    this.bindEvents()
  }

  private count = 0 // 
  private el: HTMLElement // 

  private bindEvents() {
    window.addEventListener(EVT.list.crawlStart, () => {
      this.reset()
    })

    window.addEventListener(EVT.list.downloadStop, () => {
      // 
      this.count = 0
    })

    window.addEventListener(EVT.list.skipDownload, () => {
      this.addCount()
    })

    window.addEventListener(EVT.list.downloadStart, () => {
      if (this.count === 0) {
        this.reset()
      }
    })

    window.addEventListener(EVT.list.resultChange, () => {
      this.reset()
    })

    window.addEventListener(EVT.list.downloadComplete, () => {
      // 
      this.count = 0
    })
  }

  private addCount() {
    this.count++
    lang.updateText(this.el, '_n', this.count.toString())
  }

  private reset() {
    this.count = 0
    lang.updateText(this.el, '')
  }
}

export { ShowSkipCount }
