import { EVT } from './EVT'
import { theme } from './Theme'
import { Colors } from './Colors'
import { bg } from './BG'
import { lang } from './Lang'
import { store } from './store/Store'
import { toast } from './Toast'
import { Tools } from './Tools'
import { Utils } from './utils/Utils'
import { settings } from './setting/Settings'
import { DateFormat } from './utils/DateFormat'
import { Config } from './Config'

// 
class Log {
  constructor() {
    this.scrollToBottom()

    window.addEventListener(EVT.list.clearLog, () => {
      this.clear()
    })

    const clearRecordEvents = [EVT.list.clearLog, EVT.list.downloadStop]
    clearRecordEvents.forEach((evt) => {
      window.addEventListener(evt, () => {
        this.record = []
      })
    })

    window.addEventListener(EVT.list.crawlComplete, () => {
      if (settings.exportLog && settings.exportLogTiming === 'crawlComplete') {
        this.export()
      }
    })

    window.addEventListener(EVT.list.downloadComplete, () => {
      if (
        settings.exportLog &&
        settings.exportLogTiming === 'downloadComplete'
      ) {
        this.export()
      }
    })
  }

  private id = 'logWrap' //  id
  private wrap = document.createElement('div') // 
  private logArea = document.createElement('div') // 
  //  flag 
  private refresh: { [key: string]: HTMLElement } = {
    default: document.createElement('span'),
  }
  private readonly levelColor = [
    'inherit',
    Colors.textSuccess,
    Colors.textWarning,
    Colors.textError,
  ]

  private max = 300
  private count = 0

  private record: { html: string; level: number }[] = []

  private toBottom = false //  true false

  // 
  /*
  str 
  level 
  br 
  keepShow  truefalse 

  level 
  0 normal
  1 success
  2 warning
  3 error
  */
  private add(
    str: string,
    level: number,
    br: number,
    keepShow: boolean,
    refreshFlag: string = 'default'
  ) {
    this.checkElement()
    let span = document.createElement('span')
    if (!keepShow) {
      if (this.refresh[refreshFlag] === undefined) {
        this.refresh[refreshFlag] = span
      } else {
        span = this.refresh[refreshFlag]
      }
    } else {
      this.count++
    }

    span.innerHTML = str

    span.style.color = this.levelColor[level]

    while (br > 0) {
      span.appendChild(document.createElement('br'))
      br--
    }

    this.logArea.appendChild(span)
    this.toBottom = true // 

    // 
    if (keepShow) {
      this.record.push({ html: span.outerHTML, level })
    }
  }

  public log(
    str: string,
    br: number = 1,
    keepShow: boolean = true,
    refreshFlag = 'default'
  ) {
    this.add(str, 0, br, keepShow, refreshFlag)
  }

  public success(
    str: string,
    br: number = 1,
    keepShow: boolean = true,
    refreshFlag = 'default'
  ) {
    this.add(str, 1, br, keepShow, refreshFlag)
  }

  public warning(
    str: string,
    br: number = 1,
    keepShow: boolean = true,
    refreshFlag = 'default'
  ) {
    this.add(str, 2, br, keepShow, refreshFlag)
  }

  public error(
    str: string,
    br: number = 1,
    keepShow: boolean = true,
    refreshFlag = 'default'
  ) {
    this.add(str, 3, br, keepShow, refreshFlag)
  }

  /** */
  //  0/10, 1/10, 2/10... 10/10
  //  span 
  //  10/10  span 
  // 
  public persistentRefresh(refreshFlag: string = 'default') {
    this.refresh[refreshFlag] = document.createElement('span')
  }

  private checkElement() {
    // 
    let test = document.getElementById(this.id)
    if (test === null) {
      this.wrap = document.createElement('div')
      this.wrap.id = this.id
      this.logArea = document.createElement('div')
      this.logArea.classList.add('beautify_scrollbar', 'logContent')
      if (Config.mobile) {
        this.wrap.classList.add('mobile')
      }
      this.wrap.append(this.logArea)
      document.body.insertAdjacentElement('beforebegin', this.wrap)
      theme.register(this.wrap)
      // 
      // bg.useBG(this.wrap, 0.9)
    }

    // 
    //  8000  1 GB 
    if (this.count > this.max) {
      this.clear()
    }
  }

  /** */
  public remove() {
    this.count = 0
    this.wrap.remove()
  }

  /** */
  public clear() {
    this.count = 0
    this.logArea.innerHTML = ''
  }

  // 
  private scrollToBottom() {
    window.setInterval(() => {
      if (this.toBottom) {
        this.logArea.scrollTop = this.logArea.scrollHeight
        this.toBottom = false
      }
    }, 800)
  }

  private export() {
    const data: string[] = []

    for (const record of this.record) {
      let html = ''
      if (record.level !== 3 && settings.exportLogNormal) {
        html = record.html
      }
      if (record.level === 3 && settings.exportLogError) {
        html = record.html
      }

      // 
      if (html && settings.exportLogExclude.length > 0) {
        let checkStr = html
        //  id 
        if (html.includes('<a href')) {
          const array = html.split('</a>')
          checkStr = array[array.length - 1]
        }
        const index = settings.exportLogExclude.findIndex((val) => {
          return checkStr.includes(val)
        })
        if (index === -1) {
          data.push(html)
        }
      }
    }

    if (data.length === 0) {
      return
    }

    const fileName = `log-${Utils.replaceUnsafeStr(
      Tools.getPageTitle()
    )}-${Utils.replaceUnsafeStr(
      DateFormat.format(store.crawlCompleteTime, settings.dateFormat)
    )}.html`

    const content = `<!DOCTYPE html>
<html>
<body>
<div id="logWrap">
${data.join('\n')}
</div>
</body>
</html>`

    const blob = new Blob([content], {
      type: 'text/html',
    })

    const url = URL.createObjectURL(blob)

    Utils.downloadFile(url, fileName)

    const msg = lang.transl('_')
    log.success(msg)
    toast.success(msg, {
      position: 'topCenter',
    })
  }
}

const log = new Log()
export { log }
