//  csv 
// csv  https://www.jianshu.com/p/54b3afc06126

type CSVData = any[][]

const example: CSVData = [
  ['titleA', 'titleB', 'titleC'],
  ['a1', 'b1', 'c1'],
  [[1, 2, 3], false, 456],
  [undefined, 'b,b,b', 'c c c'],
]

// any String String
// 
// 
//  Array1. 2. Symbol 

class CreateCSV {
  constructor() {
    this.utf8BOM = this.UTF8BOM()
  }

  private readonly separate = ',' // 
  private readonly CRLF = '\r\n' // 
  private utf8BOM: ArrayBuffer //  UTF-8 BOM  BOM  Excel  ANSI 
  private reg = / |\"|,|\n/ // 
  // 
  // 

  // 
  private format(arr: any[]) {
    const rowData: string[] = []
    // 
    for (let i = 0; i < arr.length; i++) {
      let colData: string = ''

      const origin = arr[i]
      const type = typeof origin

      // 
      switch (type) {
        case 'string':
          colData = origin
          break
        case 'bigint':
        case 'number':
        case 'boolean':
        case 'function':
          colData = origin.toString()
          break
        case 'undefined':
          colData = ''
          break
        case 'symbol':
          colData = origin.description || ''
          break
        case 'object':
          if (origin === null) {
            colData = ''
          } else if (Array.isArray(origin)) {
            colData = (origin as any[]).join(this.separate)
          }
          break
        default:
          throw new Error('Unkown data type')
      }

      // 
      colData = colData.replace(/\"/g, '""')
      // 
      if (this.reg.test(colData)) {
        colData = this.addQuotation(colData)
      }

      rowData.push(colData)
    }
    return rowData
  }

  public create(data: CSVData) {
    // 
    const result: string[] = []

    //  utf8BOM
    result.push(this.utf8BOM as unknown as string)

    // 
    for (const row of data) {
      result.push(this.format(row).join(this.separate))
      result.push(this.CRLF)
    }

    const csvBlob = new Blob(result, {
      type: 'text/csv',
    })
    return csvBlob
  }

  private UTF8BOM() {
    const buff = new ArrayBuffer(3)
    const data = new DataView(buff)
    data.setInt8(0, 0xef)
    data.setInt8(1, 0xbb)
    data.setInt8(2, 0xbf)
    return buff
  }

  // 
  public addQuotation(str: string) {
    return '"' + str + '"'
  }
}

const createCSV = new CreateCSV()
// createCSV.create(example)
export { createCSV }
