import { EVT } from '../EVT'
import { Tools } from '../Tools'
import { lang } from '../Lang'
import { Config } from '../Config'
import { store } from '../store/Store'
import { Result } from '../store/StoreType'
import { fileName } from '../FileName'
import { createCSV } from '../utils/CreateCSV'
import { toast } from '../Toast'
import { Utils } from '../utils/Utils'
import { showHelp } from '../ShowHelp'

// 
interface Field {
  name: string
  index: keyof Result
}
// name  csv 
// index 

//  csv 
class ExportResult2CSV {
  constructor() {
    window.addEventListener(EVT.list.exportCSV, () => {
      this.beforeCreate()
    })
  }

  // 
  private readonly fieldCfg: Field[] = [
    {
      name: 'id',
      index: 'idNum',
    },
    {
      name: 'tags',
      index: 'tags',
    },
    {
      name: 'tags_transl',
      index: 'tagsTranslOnly',
    },
    {
      name: 'user',
      index: 'user',
    },
    {
      name: 'userId',
      index: 'userId',
    },
    {
      name: 'title',
      index: 'title',
    },
    {
      name: 'description',
      index: 'description',
    },
    {
      name: 'type',
      index: 'type',
    },
    {
      name: 'page',
      index: 'pageCount',
    },
    {
      name: 'bookmark',
      index: 'bmk',
    },
    {
      name: 'bookmarked',
      index: 'bookmarked',
    },
    {
      name: 'likeCount',
      index: 'likeCount',
    },
    {
      name: 'viewCount',
      index: 'viewCount',
    },
    {
      name: 'commentCount',
      index: 'commentCount',
    },
    {
      name: 'width',
      index: 'fullWidth',
    },
    {
      name: 'height',
      index: 'fullHeight',
    },
    {
      name: 'xRestrict',
      index: 'xRestrict',
    },
    {
      name: 'AI',
      index: 'aiType',
    },
    {
      name: 'date',
      index: 'date',
    },
    {
      name: 'original',
      index: 'original',
    },
    {
      name: 'thumb',
      index: 'thumb',
    },
    // fileName  index 
    {
      name: 'fileName',
      index: 'title',
    },
  ]

  private beforeCreate() {
    // 
    if (store.result.length === 0) {
      toast.error(lang.transl('_'))
      return
    }

    //  result  resultMeta result resultMeta result 
    // 1 result resultMeta 
    // 2:  A resultMeta  A  B B  resultMeta  A 
    //  result 
    this.create(store.result)
  }

  private create(data: Result[]) {
    const body: any[][] = [] // 

    // 
    const head: string[] = []
    for (const field of this.fieldCfg) {
      head.push(field.name)
    }
    body.push(head)

    // 
    for (const d of data) {
      // 
      // 
      const index = d.index ?? Tools.getResultIndex(d)
      if (index > 0) {
        continue
      }

      const bodyItem: any[] = [] // 
      // 
      for (const field of this.fieldCfg) {
        if (field.name === 'fileName') {
          bodyItem.push(fileName.createFileName(d))
        } else {
          let result = d[field.index] ?? ''

          // 

          if (field.name === 'description') {
            result = Utils.htmlToText(Tools.replaceATag(d.description))
          }

          if (field.name === 'type') {
            result = Config.worksTypeName[result as number]
          }

          if (field.name === 'bookmarked') {
            result = (result as boolean) ? 'Yes' : 'No'
          }

          if (field.name === 'xRestrict') {
            result = Tools.getXRestrictText(result as number) || ''
          }

          if (field.name === 'AI') {
            result = Tools.getAITypeText(d.aiType || 0)
          }

          bodyItem.push(result)
        }
      }
      // 
      body.push(bodyItem)
    }

    const csv = createCSV.create(body)
    const csvURL = URL.createObjectURL(csv)

    // 
    let csvName = `result-total ${body.length - 1}-${Utils.replaceUnsafeStr(
      Tools.getPageTitle()
    )}-${Utils.replaceUnsafeStr(store.crawlCompleteTime.toLocaleString())}.csv`

    Utils.downloadFile(csvURL, csvName)

    toast.success(lang.transl('_'))

    showHelp.show('tipCSV', lang.transl('_CSV'))
  }
}

new ExportResult2CSV()
